let timer = null
import config from '@/api/config.js';
import {countdown} from "@/api/countdown.js"
export default{
	props:{
		show:{
			type:Boolean,
			default:false,
		},
		end_time:{
			type:Number
		},
		start_time:{
			type:Number
		},
		imgList:{
			type:Array,
		},
		video:{
			type:String
		}
	},
	watch:{
		start_time(value){
			this.countdowns2(value)
		},
		end_time(value){
			this.countdowns1(value)
		}
		
	},
	data(){
		return{
			num:"1",
			hr:"00",
			min:'00',
			sec:"00",
			hr1:"00",
			min1:'00',
			sec1:"00",
			autoplay:true,
			duration:1000,
			interval:2000,
		}
	},
	filters:{
		filterImg(value){
			if(value){
				return config.domain + value
			}
		}
	},
	computed:{
		changeNum(){
			if(this.video){
				return this.imgList.length + 1
			}else{
				return this.imgList.length
			}
		}
	},
	methods:{
		intervalChange(e){
			this.num = e.detail.current + 1
		},
		countdowns1(time){
			const end = new Date(parseInt(time) * 1000)
			clearInterval(timer)
			timer = setInterval(async ()=>{
				let {hr,min,sec} = await countdown(end)
				if(sec){
					this.hr = hr;
					this.min = min;
					this.sec = sec
				}else{
					clearInterval(timer)
				}	  
			},1000)
		},
		countdowns2(time){
			let times = null;
			const end = new Date(parseInt(time) * 1000)
			clearInterval(times)
			times = setInterval(async ()=>{
				let {hr,min,sec} = await countdown(end)
				if(sec){
					this.hr1 = hr;
					this.min1 = min;
					this.sec1 = sec
				}else{
					this.$emit("handleStart",true)
					clearInterval(times)
				}	  
			},1000)
		},
		handlePlay(e){
			if(e.type == "play"){
				this.autoplay = false
			}
		},
		handleEnd(e){
			if(e.type == "ended"){
				this.autoplay = true
			}
		},
		viewImages(index){
			let _this = this;
			let imgArr = [];
			_this.imgList.forEach((img,idx)=>{
				imgArr.push(config.domain + img.pic_cover)
			})
			uni.previewImage({
				current:index,
				urls: imgArr,
				indicator:"default",
				loop:true,
				longPressActions: {
					itemList: ['发送给朋友', '保存图片', '收藏'],
					success: function(data) {
						console.log('第' + (data.index + 1) + '张图片');
					},
					fail: function(err) {
						console.log(err.errMsg);
					}
				}
			});
		}
	}
}
